@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

.header_box {
  width: 100%;
  height: 100px;
  position: absolute;
  left: 0;
  top: 0;
  font-family: 'Bebas Neue', cursive;
  // background-color: pink;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header_title {
  width: 200px;
  height: 50px;
  // background-color: gray;
  display: flex;
  align-items: center;
  font-size: 30px;
  margin-left: 50px;
}

.header_index {
  width: 500px;
  height: 50px;
  // background-color: gray;
  display: flex;
  margin-right: 50px;
}

.header_index div {
  width: 33.33%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 20px;
  // background-color: blue;
}

.header_index div:hover {
  cursor: pointer;
}